        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(220, 53, 69, 0.7);
    }
    
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(220, 53, 69, 0);
    }
    
    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(220, 53, 69, 0);
    }
}

.text-input-area {
    margin-top: 15px;
    width: 100%;
}

.recitation-textarea {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 1em;
    resize: vertical;
    margin-bottom: 10px;
}

.submit-text {
    margin-top: 10px;
}

.recitation-result {
    padding: 15px;
    width: 100%;
    text-align: center;
}

.result-status {
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 20px;
    color: white;
}

.success {
    background-color: rgba(40, 167, 69, 0.8);
}

.warning {
    background-color: rgba(255, 152, 0, 0.8);
}

.result-icon {
    font-size: 2em;
    margin-bottom: 10px;
}

.accuracy, .score-display, .recognized-text, .correct-text {
    margin: 8px 0;
}

.recognized-text, .correct-text {
    font-size: 0.9em;
    text-align: left;
    background: rgba(255, 255, 255, 0.2);
    padding: 5px;
    border-radius: 5px;
    word-break: break-all;
}

